@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

a {
  text-decoration: none;
}
#root,
.pushable {
  height: 100vh;
  overflow: hidden;
}

.pusher > div {
  position: absolute;
  top: 47px;
  bottom: 40px;
  overflow-y: auto;
  width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 !important;
}

.white {
  color: white !important;
}

.sidebar-menu {
  width: 260px !important;
}
.slidbar__menu-header {
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}
.ui.vertical.menu .item:before {
  display: none !important;
}
.menu__name {
  text-align: start !important;
}
.slidebar__menu {
  display: flex !important;
  justify-content: flex-start;
  font-size: 14px;
  cursor: pointer;
}
.menu__icon {
  margin-right: 7px !important;
}
.menu__accordion {
  width: 100% !important;
}
.menu__item {
  padding: 13px 16px !important;
}

.menu__accordion-content {
  text-align: start !important;
  font-size: 14px !important;
  padding: 13px 24px !important;
  color: white !important;
}

.menu__link {
  cursor: pointer;
  transition: opacity 0.2s linear;
}
.menu__link:hover {
  opacity: 0.8;
}

.header-menu {
  height: 4rem;
  display: flex;
}
.header-menu > div:last-child {
  position: fixed;
}
.menu-icon {
  cursor: pointer;
  transition: opacity 0.2s linear;
}
.menu-icon:hover {
  opacity: 0.7 !important;
}
.header__search {
  margin-left: 20px !important;
}
.ui.dropdown > i {
  display: none;
}
.icon-content {
  margin-right: 10px;
}
.header__user {
  transition: opacity 0.2s linear;
}
.header__user:hover {
  opacity: 0.7;
}
.user__dropdown {
  margin-right: 10px;
}
.user__dropdown-menu {
  width: 180px !important;
  left: -100px !important;
  top: 50px !important;
}
.dropdown__user-info {
  height: 50px !important;
  padding: 0 0 !important;
  display: flex !important;
  align-items: center;
  justify-content: space-between;
}

.dropdown__user-info div:last-child h2 {
  font-size: 13px;
}
.dropdown__user-info div:first-child {
  width: 3em;
  height: 3em;
  display: flex;
  align-items: center;
}
.dropdown__user-info div:first-child i::before {
  font-size: 2.5em !important;
}
.dropdown__user-info div:last-child p {
  font-size: 11px;
  color: gray;
}
@media (max-width: 426px) {
  .user__dropdown-menu {
    left: -130px !important;
  }
}
@media (max-width: 321px) {
  .input__search__header {
    width: 160px;
  }
}

#section-content {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  color: black;
  height: 100%;
}

